.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.upload-section {
  text-align: center;
}

.dropzone {
  border: 2px dashed #ccc;
  border-radius: 8px;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  margin: 2rem 0;
}

.dropzone:hover {
  border-color: #2563eb;
}

.upload-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.start-button {
  background-color: #2563eb;
  color: white;
  padding: 0.75rem 2rem;
  border-radius: 6px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
}

.start-button:disabled {
  background-color: #93c5fd;
  cursor: not-allowed;
}

.file-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  border: 1px solid #e5e7eb;
  margin: 0.5rem 0;
  border-radius: 4px;
}

.results-container {
  margin-top: 2rem;
}

.leaderboard-entry {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  border: 1px solid #e5e7eb;
  margin: 1rem 0;
  border-radius: 8px;
}

.rank {
  font-size: 1.5rem;
  font-weight: bold;
  color: #2563eb;
}

.candidate-details {
  flex: 1;
}

.analysis {
  margin-top: 1rem;
}

.helper-text {
  color: #666;
  margin-top: 1rem;
}

.upload-subtitle {
  color: #666;
  font-size: 0.9rem;
}

.remove-button {
  background: none;
  border: none;
  color: #ef4444;
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  font-size: 1rem;
}

.remove-button:hover {
  color: #dc2626;
}

.uploaded-files {
  margin-top: 2rem;
  text-align: left;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.pros ul, .cons ul {
  margin: 0.5rem 0;
  padding-left: 1.5rem;
}

.pros li, .cons li {
  margin: 0.25rem 0;
}

.justification {
  margin-top: 1rem;
  line-height: 1.5;
}

.score {
  font-size: 1.25rem;
  color: #2563eb;
  margin: 0.5rem 0;
}

header {
  margin-bottom: 2rem;
  padding: 1rem;
  background-color: #f8fafc;
  border-bottom: 1px solid #e2e8f0;
}

header h1 {
  margin: 0;
  color: #1e40af;
}

.refresh-button {
  background: none;
  border: none;
  color: #2563eb;
  cursor: pointer;
  padding: 0.5rem;
  font-size: 0.9rem;
}

.refresh-button:hover {
  text-decoration: underline;
}

.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem 0;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #2563eb;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
} 